.greetingForm {
    font-family: 'Montserrat', sans-serif;
    max-width: 1280px;
    margin-left: 220px;
    margin-top: 40px;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
}

.inputAndButtonContainer {
    display: inline-flex;
}

.error {
    color: red;
}

.input {
    width: 370px;
    height: 35px;
    border: 1px solid #D1D1D1;
    color: black;
    padding-left: 12px;
    border-radius: 3px;

}

.input:focus {
    border: 1px solid #D1D1D1;
    width: 370px;
    height: 35px;
}

.errorInput {
    border: 1px solid #CC0000;
    width: 370px;
    height: 35px;
    border-radius: 3px;
}

.button {
    border-radius: 3px;
    height: 35px;
    padding: 5px 24px;
    justify-content: center;
    align-items: center;
    margin-left: 12px;
    flex-shrink: 0;
    color: white;
    background-color: #06C;
    border: none;
}

.button:focus {

}

.button:active {


}

.button:disabled {
    opacity: 0.5;
}

.text {
    color: #00000080;
    margin-bottom: 10px;
}

.greeting {
    color: black;
    margin-top: 10px;
}
