.header {
    margin: 0 auto;
    max-width: 1280px;
    height: 60px;
    background-color: white;
    display: flex;
    justify-content: space-between;
    box-shadow: 0 0 2px rgba(29, 33, 38, 0.1);
}

.header h1 {
    margin-top: 18px;
    font-family: 'Montserrat', sans-serif;;
    font-size: 24px;
    font-weight: 600;
    line-height: 30px;
    text-align: left;
}